@use "/src/utilities/variables";

// Dimensions
$dot-size: 1px;
$dot-space: 22px;
$canvas-size: 450px;

.canvasTop {
  width: 100%;
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  margin: 0 auto;
}

.canvas {
  background: linear-gradient(
        90deg,
        map-get(variables.$colors, "bg_color") ($dot-space - $dot-size),
        transparent 1%
      )
      center,
    linear-gradient(
        map-get(variables.$colors, "bg_color") ($dot-space - $dot-size),
        transparent 1%
      )
      center,
    map-get(variables.$colors, "console_red_light");
  background-size: $dot-space $dot-space;
  width: 96%;
  height: ($canvas-size + 30) px;
  margin: 0 auto;
  animation: blinker 2s linear infinite;
}

Canvas {
  height: $canvas-size !important;
}

@media only screen and (max-width: 500px) {
  $canvas-size: 300px;
}

.siteName {
  width: 96%;
  font-size: 30px;
  font-weight: 600;
  margin: 0 auto;
}

.connecting {
  z-index: 1;
  width: 130px;
  background-color: map-get(variables.$colors, "console_red_light");
  text-align: right;
  padding: 5px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  font-weight: 600;
  margin-right: 20px;
}

.flex_container {
  width: 96%;
  height: 60px;
  display: flex;
  border-bottom: 3px solid map-get(variables.$colors, "console_green_light");
  margin-bottom: 10px;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 auto;
}

.flex_child {
  justify-content: center;
  font-size: 1rem;
  border: 4px;
}

.lds {
  display: inline-block;
  width: 100%;
  height: 100%;
  margin: 0 auto;
}
.lds:after {
  content: " ";
  display: inline-block;
  width: 10px;
  height: 10px;
  margin: 2px;
  border-radius: 50%;
  border: 3px solid map-get(variables.$colors, "console_green_light");
  border-color: map-get(variables.$colors, "console_green_light") transparent
    map-get(variables.$colors, "console_green_light") transparent;
  animation: lds 1.2s linear infinite;
}
@keyframes lds {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.black {
  background-color: map-get(variables.$colors, "bg_color");
  height: 20px;
  width: 20px;
  justify-content: center !important;
}

@keyframes blinker {
  50% {
    opacity: 0.7;
  }
}

//text
.scrollText {
  height: 50px;
  width: 40vw;
  overflow: hidden;
}
.scrollText p {
  margin: 0;
  text-align: center;
  /* Starting position */
  -moz-transform: translateX(-200%);
  -webkit-transform: translateX(-200%);
  transform: translateX(-200%);
  /* Apply animation to this element */
  -moz-animation: scrollText 15s linear infinite;
  -webkit-animation: scrollText 15s linear infinite;
  animation: scrollText 15s linear infinite;
}

/* Move it (define the animation) */
@-moz-keyframes scrollText {
  0% {
    -moz-transform: translateX(-300%);
  }
  100% {
    -moz-transform: translateX(100%);
  }
}
@-webkit-keyframes scrollText {
  0% {
    -webkit-transform: translateX(-300%);
  }
  100% {
    -webkit-transform: translateX(100%);
  }
}
@keyframes scrollText {
  0% {
    -moz-transform: translateX(-200%); /* Firefox bug fix */
    -webkit-transform: translateX(-200%); /* Firefox bug fix */
    transform: translateX(-200%);
  }
  100% {
    -moz-transform: translateX(100%); /* Firefox bug fix */
    -webkit-transform: translateX(100%); /* Firefox bug fix */
    transform: translateX(100%);
  }
}
